<template>
  <div>
    <Page>
      <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
            <date-picker
              v-model="time1"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
            <date-picker
              v-model="time2"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Agentes" class="col-md-auto">
            <b-select value="Select state" :options="agents" v-model="agent">
              <b-select-option value="">Todos os Agentes</b-select-option>
            </b-select>
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <ladda-btn
            :loading="loading"
            id="showBtn"
            data-style="expand-left"
            class="btn btn-success"
            @click.native="getLoginPause"
            >Visualizar</ladda-btn
          >
        </b-form-row>
      </b-form>
    </Page>
    <Page :hidden="table.length == 0">
      <Table
        :tableData="table"
        :filename="filename"
        :tablefooter="footer"
        :header="header"
        :legend="legend"
      />
    </Page>
  </div>
</template>
<script>
import Page from "@/components/Page";
import DatePicker from "vue2-datepicker";
import "@/vendor/libs/vue2-datepicker/locale/pt-br";
import moment from "moment";
import LaddaBtn from "@/vendor/libs/ladda/Ladda";
import axios from "axios";
import Table from "@/components/Table";
import _ from "lodash";

export default {
  name: "login",
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table,
  },
  metaInfo: {
    title: "Relatório de Login/Pausas por Agente",
  },
  data: () => ({
    time1: moment().hour(0).minute(0).second(0).toDate(),
    time2: moment().endOf("day").toDate(),
    agents: [],
    agent: "",
    loading: false,
    table: [],
    footer: [],
    header: [],
    legend: [],
    filename: "",
  }),
  mounted() {
    this.$store.commit("setTitle", "Relatório de Login/Pausas Detalhado por Agente");
    this.loadAgents();
  },
  methods: {
    updateHeader() {
      this.header = [];
      this.header.push("<h4>Relatório de Login/Pausas Detalhado por Agente</h4>");
      this.header.push(
        `Período ${moment(this.time1).format("DD/MM/YYYY")} à ${moment(this.time2).format(
          "DD/MM/YYYY"
        )} / Hora à ${moment(this.time1).format("HH:mm:ss")} até ${moment(
          this.time2
        ).format("HH:mm:ss")}`
      );
      this.header.push(" ");
      this.filename = `Rel_Login_Pausas ${moment().format("DD_MM_YYYY_HH_mm_ss")}`;

      this.legend = [
        "<h5>Legenda:</h5>",
        "<b>Agentes</b> - Nome ou ramal do operador;",
        "<b>Eventos</b> - Tipo de pausas;",
        "<b>Pausas</b> - Detalhes das pausas;",
        "<b>Tempo A</b> - Tempo das pausas;",
        `<b>Emitido em:</b> ${moment().format("DD/MM/YYYY HH:mm:ss")}`,
      ];
    },
    loadAgents: function () {
      axios
        .post(`${location.origin}/reportsbackend/agents.php`)
        .then((data) => {
          this.agents = [];

          data.data.forEach((x) => {
            this.agents.push(x.agent);
          });
        })
        .catch((err) => console.log(err));
    },
    getParams: function () {
      const params = new URLSearchParams();
      params.append("time1", moment(this.time1).format("YYYY-MM-DD HH:mm:ss"));
      params.append("time2", moment(this.time2).format("YYYY-MM-DD HH:mm:ss"));
      params.append("agent", this.agent);
      return params;
    },
    getLoginPause: function () {
      this.loading = true;
      const params = this.getParams();
      axios
        .post(`${location.origin}/reportsbackend/report_login.php`, params)
        .then((data) => {
          this.getAudit(data.data);
          this.updateHeader();
        })
        .catch((err) => "opa" + console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
    getAudit(tableLogin) {
      const params = this.getParams();
      axios
        .post(`${location.origin}/reportsbackend/report_login_audit.php`, params)
        .then((data) => {
          if (tableLogin.length === 0 && data.data.length === 0) {
            this.$bvToast.toast("Nenhum registro encontrado", {
              title: "Tecnomafer Report",
              autoHideDelay: 5000,
              appendToast: true,
              toastClass: "bs4-toast bg-danger",
            });
          }
          const result = [...tableLogin, ...data.data];

          this.table = _.sortBy(result, ["Data"], ["desc"]);

          this.footer = ["Total"];
          this.footer.push(result.length);
          this.footer.push("");
          let totalTime = "00:00:00";
          let contTotalInterval = 0;
          result.forEach((x) => {
            if (x.Tempo !== "") {
              contTotalInterval++;
              const [hours, minutes, seconds] = x.Tempo.split(":").map(Number);
              totalTime = moment
                .duration(totalTime, "hh:mm:ss")
                .add({ hours, minutes, seconds });
            }
          });
          this.footer.push(contTotalInterval);
          let totalPrint = "";
          if (totalTime.days() > 0) {
            totalPrint += totalTime.days() + " Dias ";
          }
          totalPrint +=
            ("0" + totalTime.hours()).slice(-2) +
            ":" +
            ("0" + totalTime.minutes()).slice(-2) +
            ":" +
            ("0" + totalTime.seconds()).slice(-2);

          this.footer.push(totalPrint);
          this.footer.push("");
        })
        .catch((err) => "opa" + console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
  },
};
</script>
